.bloc_home {
  max-width: 100%;
  height: fit-content;
  padding-top: 140px;
  background-color: rgba(255, 255, 255, 0);
  border-bottom: solid black 2px;
}
.backgroundVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  background-size: cover;
}
.homeContainer {
  display: flex;
  justify-content: space-around;
}
.blocText {
  width: 600px;
  padding-top: 3.5rem;
  font-size: 25px;
  text-align: center;
}
.photoDeMoi {
  max-width: 600px;
}

.title_home {
  color: white;
  text-shadow: black 10px 7px 10px;
  font-size: 50px;
  text-align: center;
  height: fit-content;
}
.subTitle_home {
  color: white;
  text-shadow: black 10px 7px 10px;
  font-size: 30px;
  text-align: center;
  height: fit-content;
}
.List {
  display: flex;
  flex-direction: column;
}
.list_lang {
  display: flex;
  align-items: center;
  list-style-type: none;
  color: white;
  text-shadow: black 10px 7px 10px;
}
.list_lang_logo {
  width: 50px;
  margin-right: 20px;
}

/* Mobile --------------------------------------------------------------------*/

